.registration-box {
  #box();

  &__inner {
    #box.inner();
  }

  &__row {
    #box.row();
  }

  &__title {
    #box.title();
  }

  &__input-text {
    #box.input-text();
  }

  &__item {
    #box.item();
  }

  &__alternate-entry {
    #box.alternate-entry();
  }

  &__text {
    #box.text();
  }
}
